import "./Input.css";

function Input(props) {


  return (
    <div id="container">
      <div>
        <input
          className="inputs"
          placeholder="Title"
          type="text"
          alt="A place to enter the title of your note"
          onChange={(e) => props.titleText(e.target.value)}
          value={props.titleValue}
        ></input>
      </div>
      <div>
        <input
          className="inputs"
          placeholder="Body"
          type="text"
          alt="A place to enter the body of your note"
          onChange={(e) => props.bodyText(e.target.value)}
          value={props.bodyValue}
        ></input>
      </div>
    </div>
  );
}

export default Input;
