import './Notes.css'
import Note from './Note'


const Notes = (props) => {

const notes = props.notes;

let id = props.id;

let x = 0;




return (
<div id="notesbox">

{notes.map((note) => (
    <Note notes={notes} id={id} key={x++} note={note} delete={props.delete} />
))}
   
</div>
)
}

export default Notes;