import { FiDelete } from "react-icons/fi";
import "./Note.css";


const Note = (props) => {
  const note = props.note;

  let id = props.id;

  let deleteFunc = props.delete;



  return (
    <div className="notes">
        <span id={id} onClick={() => deleteFunc(note.id)}><FiDelete
          style={{
            color: "black",
            fontSize: "22px",
            position: "fixed",
            top: "2",
            right: "5",
            cursor: 'pointer'
          }}
        />
        </span>
      <h3>{note.title}</h3>
      <p style={{ fontSize: "16px" }}>{note.body}</p>
    </div>
  );
};

export default Note;
