import "./App.css";
import Input from "./Input";
import SubmitBtn from "./Submit";
import Notes from "./Notes";
import React, { useState } from "react";
import DeleteAll from "./DeleteAll";
import { useEffect } from 'react';


function App() {



  const [notes, setNotes] = useState([]);

  const [display, setDisplay] = useState(false);

  const [title, setTitle] = useState("");

  const [body, setBody] = useState("");

  const [id, setId] = useState(0);

  

  class Note {
    constructor(title, body, id) {
      this.title = title;
      this.body = body;
      this.id = id;
    }
  }

  const createNote = (e) => {
    e.preventDefault();

    let note = new Note(title, body, id);

    console.log(note.id);

    setNotes([...notes, note]);

    setTitle("");

    setBody("");

    setId(id + 1);

    localStorage.setItem(note, JSON.stringify(note));
  };

  const deleteNote = (id) => {
    setNotes(notes.filter((note) => note.id !== id));
  };

  const handleDeleteAll = () => {
    setNotes([]);
    localStorage.clear();
    setTitle("");
    setBody("");
  }

  

  useEffect(() => {
    notes.length > 0 ? setDisplay(true) : setDisplay(false)
  }, [notes.length])

 
  return (
    <div className="App">
      <Input
        titleText={setTitle}
        bodyText={setBody}
        titleValue={title}
        bodyValue={body}
      />

      <SubmitBtn create={createNote}/>

      <Notes
        notes={notes}
        delete={deleteNote}
        id={id - 1}
      />

      {display === true ? <DeleteAll deleteAll={handleDeleteAll} /> : ''}
    </div>
  );
}

export default App;
